//Bebas Bold
@include font("Bebas Bold", "bebasneue_bold");
@mixin bebas-bold {
	font-family: "Bebas Bold", Arial, Helvetica, sans-serif;
	font-weight: normal; }

//Bebas Regular
@include font("Bebas Regular", "bebasneue_regular");
@mixin bebas-reg {
	font-family: "Bebas Regular", Arial, Helvetica, sans-serif;
	font-weight: normal; }

//Evogria
@include font("Evogria", "evogria");
@mixin evo {
	font-family: "Evogria", Arial, Helvetica, sans-serif;
	font-weight: normal; }
